.home-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.button-container {
  display: flex;
  gap: 20px;
  margin-top: 20px;
}

.login-button,
.register-button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #4caf50; /* Green */
  color: white;
  border: none;
  border-radius: 5px;
  text-decoration: none;
  cursor: pointer;
}

.login-button:hover,
.register-button:hover {
  background-color: #45a049; /* Darker green on hover */
}
